export default {
	data() {
		return {
			UserInfo: {
				// avatar: "https://tfs.alipayobjects.com/images/partner/TB1c1dbbz9EDuNjmgXUXXbCKXXa",
				// nickName: "文保"
			},
			avatar: '',
			nickName: ''
		};
	},
	computed: {

	},
	onLoad: function() {
		this.getUserInfoMt()
		console.log("sdhsnhsighsdui", this.UserInfo)
	},
	onShow: function() {
		// console.log("pages/user/home");

	},
	methods: {
		getUserInfoMt() {

			my.getAuthCode({
				scopes: 'auth_user',
				success: (auth) => {
					uni.getUserInfo({
						provider: 'alipay',
						success: (result) => {
							console.log('getUserInfo success', result);
							this.UserInfo = result
						},
						fail: (error) => {
							console.log('getUserInfo fail', error);
							let content = error.errMsg;
							if (~content.indexOf('uni.login')) {
								content = '请在登录页面完成登录操作';
							}
						}
					});
				},
				fail: (error) => {
					console.log(error)
				}
			});

		},
		goTel() {
			uni.makePhoneCall({
				// 手机号
				phoneNumber: '110119',
				// 成功回调
				success: (res) => {
					console.log('调用成功!')
				},
				// 失败回调
				fail: (res) => {
					console.log('调用失败!')
				}
			});
		}
	}
};
